.circleStyle{
  max-width: 150;
  display:'inline-block';
  margin:2px;
  position:'relative';
  bottom:1px;
}

div#form{
  box-shadow: 0 0 5px 2px gray;
  margin: 20px;
  width: 40%;
  padding: 20px;
}

div.messageBar{
  box-shadow: 0 0 5px 2px gray;
  margin: 20px;
  padding: 10px;
  text-align: center;
  vertical-align: text-bottom;

}